import React, { useState, useRef, useEffect } from "react";
import { cn } from "$Popup/lib/utils";
import BGSvgIcon from "$Popup/components/core/BGSvgIcon";

interface SearchHeaderProps {
  value: string;
  onChange: (value: string) => void;
  placeholder?: string;
  className?: string;
}

export const SearchHeader: React.FC<SearchHeaderProps> = React.memo(({
  value,
  onChange,
  placeholder = "Search...",
  className
}) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (isExpanded && inputRef.current) {
      inputRef.current.focus();
    }
  }, [isExpanded]);

  const handleToggle = () => {
    if (isExpanded) {
      // If expanded, clear the search and close
      onChange("");
      setIsExpanded(false);
    } else {
      // If collapsed, expand
      setIsExpanded(true);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    onChange(e.target.value);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Escape') {
      onChange("");
      setIsExpanded(false);
    }
  };

  return (
    <div className={cn("flex items-center justify-end relative", className)}>
      {isExpanded ? (
        <div className="absolute right-0 top-1/2 -translate-y-1/2 z-10 flex items-center bg-backdrop border border-tragic-200 rounded-4xl overflow-hidden focus-within:border-primary focus-within:border-1 focus-within:shadow-[0_0_0_4px_var(--color-primary-100)]">
          <input
            ref={inputRef}
            type="text"
            value={value}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
            placeholder={placeholder}
            className="px-3 py-1 text-sm font-normal bg-transparent outline-none w-45 text-tragic-400"
          />
        </div>
      ) : null}
      
      <button
        onClick={handleToggle}
        className="p-1 hover:bg-tragic-100 rounded-4xl transition-colors relative z-20 mr-1 flex-shrink-0"
        aria-label={isExpanded ? "Close search" : "Search"}
      >
        <BGSvgIcon className="mr-1 flex-shrink-0" iconName={isExpanded ? "close" : "magnifier"} size={15}/>
      </button>
    </div>
  );
});

SearchHeader.displayName = 'SearchHeader';
