import { BROWSER_NAME } from '@/app/scripts/app-consts';
import React from 'react'

/**
 * Props for the BGSvgIcon component
 * @param iconName - Name of the icon to display
 * @param size - Size of the icon, can be a number or string
 * @param height - Height of the icon, can be a number or string
 * @param width - Width of the icon, can be a number or string
 * @param className - Additional CSS classes to apply to the icon
 * @param alt - Alternative text for the icon
 * @param isActive - Whether the icon is active (applies saturation)
 * @param forceFill - Force a specific color over the icon (CSS color value, hex, or Tailwind color class like 'primary-400')
 * @param iconRoot - The root path to the icon (used for situations where the component is embedded in a foreign domain)
 * @returns
 */

// Safe way to check browser without build-time dependency issues
let isFF = false;
// eslint-disable-next-line @typescript-eslint/no-var-requires
const browserConst = require('@/app/scripts/browser-const');
isFF = (browserConst.TARGET_BROWSER as string) === BROWSER_NAME.Firefox;


const BGSvgIcon: React.FC<{
  iconName: string
  size?: number | string
  height?: number | string
  width?: number | string
  className?: string
  alt?: string
  isActive?: boolean
  isNeutral?: boolean, 
  iconRoot?: string
  forceFill?: string
}> = ({
  iconName,
  size = 24,
  height = '',
  width = '',
  className = '',
  isNeutral = false,
  alt = 'icon',
  isActive = true,
  forceFill,
  iconRoot = null,
}) => {
    const [svgContent, setSvgContent] = React.useState<string>('')
    const [isDarkMode, setIsDarkMode] = React.useState(false)
    const [hasNeutral, setHasNeutral] = React.useState<boolean | null>(null)

    // Helper function to resolve CSS custom properties and Tailwind color classes
    const resolveCSSVariable = (value: string): string => {
      // Handle CSS custom properties
      if (value.startsWith('var(--') || value.startsWith('--')) {
        const varName = value.startsWith('var(--')
          ? value.slice(4, -1) // Remove 'var(' and ')'
          : value.slice(2) // Remove '--'

        const computedValue = getComputedStyle(document.documentElement)
          .getPropertyValue(`--${varName}`)
          .trim()

        return computedValue || value
      }

      // Handle Tailwind-style color classes (e.g., 'primary-400', 'danger', 'success-200', 'selected-chip')
      if (value.match(/^[a-z]+(-[a-z]+)*(-\d+)?$/)) {
        const cssVarName = `--color-${value}`

        const computedValue = getComputedStyle(document.documentElement)
          .getPropertyValue(cssVarName)
          .trim()

        return computedValue || value
      }

      return value
    }

    // Check for dark mode preference
    React.useEffect(() => {
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)')
      setIsDarkMode(mediaQuery.matches)

      const handleChange = (e: MediaQueryListEvent) => setIsDarkMode(e.matches)
      mediaQuery.addEventListener('change', handleChange)

      return () => mediaQuery.removeEventListener('change', handleChange)
    }, [])

    React.useEffect(() => {
      let isCancelled = false
      const neutralPath = iconRoot ? `${stripTrailingSlash(iconRoot)}/assets/icons/neutral/${iconName}.svg` : `./assets/icons/neutral/${iconName}.svg`
      fetch(neutralPath, { cache: 'no-store' })
        .then(async (res) => {
          if (!isCancelled) {
            if (isFF) {
              // Firefox can return res.ok=true for missing files, so we must verify content
              try {
                const text = await res.text()
                // Check if we got actual SVG content (not an error page or empty response)
                const isValidSvg = text.length > 0 &&
                  text.trim().startsWith('<') &&
                  (text.includes('<svg') || text.includes('<?xml'))
                setHasNeutral(isValidSvg)
              } catch (err) {
                setHasNeutral(false)
              }
            } else {
              setHasNeutral(res.ok)
            }
          }
        })
        .catch(() => {
          if (!isCancelled) setHasNeutral(false)
        })
      return () => {
        isCancelled = true
      }
    }, [iconName])

    // Fetch SVG content when forceFill is used
    React.useEffect(() => {
      if (!forceFill) return
      if (hasNeutral === null) return // wait until we know if neutral exists

      const base = hasNeutral
        ? 'neutral'
        : isDarkMode
          ? 'dark-mode'
          : 'light-mode'
      const svgPath = iconRoot ? `${stripTrailingSlash(iconRoot)}/assets/icons/${base}/${iconName}.svg` : `./assets/icons/${base}/${iconName}.svg`

      fetch(svgPath)
        .then((response) => response.text())
        .then((svg) => {
          const resolvedColor = resolveCSSVariable(forceFill)
          const modifiedSvg = svg
            .replace(/fill="[^"]*"/g, `fill="${resolvedColor}"`)
            .replace(/fill:[^;]*;/g, `fill:${resolvedColor};`)
            .replace(/stroke="[^"]*"/g, `stroke="${resolvedColor}"`)
            .replace(/stroke:[^;]*;/g, `stroke:${resolvedColor};`)
          setSvgContent(modifiedSvg)
        })
        .catch((err) => console.warn('Failed to load SVG:', err))
    }, [iconName, forceFill, hasNeutral, isDarkMode])

    const getDim = (dim: number | string, size: number | string) => {
      // check if dim is a valid number or string as number, if so return it as a string with 'px' appended else return size
      if (
        typeof dim === 'number' ||
        (typeof dim === 'string' && !isNaN(parseInt(dim)))
      ) {
        return `${dim}px`
      }
      return size
    }

    const neutralPath = iconRoot ? `${stripTrailingSlash(iconRoot)}/assets/icons/neutral/${iconName}.svg` : `./assets/icons/neutral/${iconName}.svg`
    const darkModePath = iconRoot ? `${stripTrailingSlash(iconRoot)}/assets/icons/dark-mode/${iconName}.svg` : `./assets/icons/dark-mode/${iconName}.svg`
    const lightModePath = iconRoot ? `${stripTrailingSlash(iconRoot)}/assets/icons/light-mode/${iconName}.svg` : `./assets/icons/light-mode/${iconName}.svg`
    // console.table({ 'iconRoot': iconRoot, 'darkModePath': darkModePath, 'lightModePath': lightModePath, 'neutralPath': neutralPath })

    // If forceFill is used and we have SVG content, render inline SVG
    if (forceFill && svgContent) {
      return (
        <div
          className={`${className} ${!isActive ? 'saturate-0' : ''}`}
          style={{
            width: getDim(width, size),
            height: getDim(height, size),
            display: 'inline-block',
          }}
          dangerouslySetInnerHTML={{ __html: svgContent }}
          aria-label={alt}
        />
      )
    }

    // If a neutral icon exists, always use it; otherwise, fall back to theme-based sources
    if (hasNeutral) {
      return (
        <img
          src={neutralPath}
          width={getDim(width, size)}
          height={getDim(height, size)}
          className={`${className} ${!isActive ? 'saturate-0' : ''}`}
          alt={alt}
          draggable={false}
        />
      )
    }

    return (
      <picture>
        <source srcSet={darkModePath} media="(prefers-color-scheme: dark)" />
        <source
          srcSet={lightModePath}
          media="(prefers-color-scheme: light), (prefers-color-scheme: no-preference)"
        />
        <img
          src={lightModePath}
          width={getDim(width, size)}
          height={getDim(height, size)}
          className={`${className} ${!isActive ? 'saturate-0' : ''}`}
          alt={alt}
          draggable={false}
        />
      </picture>
    )
  }

function stripTrailingSlash(path: string) {
  return path.replace(/\/$/, '')
}

export default BGSvgIcon
