import { useCallback, useMemo } from 'react'
import { useNavigate, useLocation } from 'react-router-dom'
import { ROUTES, RouteKey, DEFAULT_ROUTE } from './constants'
import { NavigationUtils, UseAppNavigationReturn } from './types'

/**
 * Custom hook for hash-based navigation utilities
 *
 * Provides type-safe navigation methods and utilities
 * for the Browser Guard extension hash-based SPA routing.
 */

// Navigation utilities for hash-based routing
export const navigationUtils: NavigationUtils = {
  /**
   * Navigate to a route using hash navigation
   * This updates the hash fragment in the URL
   */
  navigateToRoute: (
    routeKey: RouteKey,
    queryParams?: Record<string, string>
  ): void => {
    try {
      const route = ROUTES[routeKey]
      if (!route) {
        console.warn(`BGLink: Invalid route key "${routeKey}"`)
        return
      }

      let hash = route.path

      if (queryParams && Object.keys(queryParams).length > 0) {
        const params = new URLSearchParams(queryParams)
        hash += `?${params.toString()}`
      }

      window.location.hash = hash
    } catch (error) {
      console.error('BGLink: Failed to navigate to route:', error)
    }
  },

  /**
   * Navigate to a custom path using hash navigation
   */
  navigateToPath: (
    path: string,
    queryParams?: Record<string, string>
  ): void => {
    try {
      if (!path) {
        console.warn('BGLink: Empty path provided')
        return
      }

      let hash = path.startsWith('/') ? path : `/${path}`

      if (queryParams && Object.keys(queryParams).length > 0) {
        const params = new URLSearchParams(queryParams)
        hash += `?${params.toString()}`
      }

      window.location.hash = hash
    } catch (error) {
      console.error('BGLink: Failed to navigate to path:', error)
    }
  },

  /**
   * Get the current hash path (without the # symbol)
   */
  getCurrentHashPath: (): string => {
    try {
      const hash = window.location.hash
      if (!hash || hash === '#') {
        return DEFAULT_ROUTE.path
      }

      // Remove # and any query parameters
      const path = hash.slice(1).split('?')[0]
      return path || DEFAULT_ROUTE.path
    } catch (error) {
      console.error('BGLink: Failed to get current hash path:', error)
      return DEFAULT_ROUTE.path
    }
  },

  /**
   * Get the current full hash (including # symbol)
   */
  getCurrentHash: (): string => {
    try {
      return window.location.hash || DEFAULT_ROUTE.hash
    } catch (error) {
      console.error('BGLink: Failed to get current hash:', error)
      return DEFAULT_ROUTE.hash
    }
  },

  /**
   * Check if we're currently on a specific route
   */
  isCurrentRoute: (routeKey: RouteKey): boolean => {
    try {
      const route = ROUTES[routeKey]
      if (!route) {
        console.warn(`BGLink: Invalid route key "${routeKey}"`)
        return false
      }

      const currentPath = navigationUtils.getCurrentHashPath()
      return currentPath === route.path
    } catch (error) {
      console.error('BGLink: Failed to check current route:', error)
      return false
    }
  },

  /**
   * Check if we're currently on a specific path
   */
  isCurrentPath: (path: string): boolean => {
    try {
      if (!path) return false

      const currentPath = navigationUtils.getCurrentHashPath()
      return currentPath === path
    } catch (error) {
      console.error('BGLink: Failed to check current path:', error)
      return false
    }
  },

  /**
   * Get query parameters from current hash URL
   */
  getQueryParams: (): URLSearchParams => {
    try {
      const hash = window.location.hash
      const queryString = hash.includes('?') ? hash.split('?')[1] : ''
      return new URLSearchParams(queryString)
    } catch (error) {
      console.error('BGLink: Failed to get query params:', error)
      return new URLSearchParams()
    }
  },

  /**
   * Check if the current route is valid
   */
  isValidRoute: (): boolean => {
    try {
      const currentPath = navigationUtils.getCurrentHashPath()
      return Object.values(ROUTES).some((route) => route.path === currentPath)
    } catch (error) {
      console.error('BGLink: Failed to validate route:', error)
      return false
    }
  },
}

export const useAppNavigation = (): UseAppNavigationReturn => {
  const navigate = useNavigate()
  const location = useLocation()

  // Memoized type-safe navigation methods for React Router
  const navigateTo = useMemo(
    () => ({
      home: () => navigate(ROUTES.HOME.path),
      settings: () => navigate(ROUTES.SETTINGS.path),
      dashboard: () => navigate(ROUTES.DASHBOARD.path),
      allowList: () => navigate(ROUTES.ALLOW_LIST.path),
      detectionHistory: () => navigate(ROUTES.DETECTION_HISTORY.path),
      importAndExport: () => navigate(ROUTES.IMPORT_AND_EXPORT.path),
      profileChecker: () => navigate(ROUTES.PROFILE_CHECKER.path),
      contentControl: () => navigate(ROUTES.CONTENT_CONTROL.path),
    }),
    [navigate]
  )

  // Memoized utility to check current route
  const isCurrentRoute = useCallback(
    (path: string): boolean => {
      return location.pathname === path
    },
    [location.pathname]
  )

  // Memoized utility to navigate with state (within React Router)
  const navigateWithState = useCallback(
    (path: string, state?: any) => {
      navigate(path, { state })
    },
    [navigate]
  )

  // Memoized utility to go back
  const goBack = useCallback(() => {
    navigate(-1)
  }, [navigate])

  // Memoized utility to replace current route
  const replace = useCallback(
    (path: string) => {
      navigate(path, { replace: true })
    },
    [navigate]
  )

  return useMemo(
    () => ({
      // React Router navigation
      navigate,
      navigateTo,
      location,
      isCurrentRoute,
      navigateWithState,
      goBack,
      replace,

      // Hash utilities (for BGLink component)
      utils: navigationUtils,
    }),
    [
      navigate,
      navigateTo,
      location,
      isCurrentRoute,
      navigateWithState,
      goBack,
      replace,
    ]
  )
}

export default useAppNavigation
