// Type for route keys
export type RouteKey = keyof typeof ROUTES;

// Type for route object
export type RouteConfig = typeof ROUTES[RouteKey];

// Helper function to get route by path
export const getRouteByPath = (path: string): RouteConfig | undefined => {
  return Object.values(ROUTES).find(route => route.path === path);
};

// Helper function to get route by hash
export const getRouteByHash = (hash: string): RouteConfig | undefined => {
  const cleanHash = hash.startsWith('#') ? hash : `#${hash}`;
  return Object.values(ROUTES).find(route => route.hash === cleanHash);
};

// Default route
export const DEFAULT_ROUTE = ROUTES.HOME;
