import React, { useState, useRef, useEffect, createContext, useContext } from 'react';
import { createPortal } from 'react-dom';
import { MenuItem, MenuProps } from './types';
import MenuButton from './MenuButton';

/**
 * Menu Component - A reusable dropdown menu that can accept either menu items or custom HTML content
 * 
 * Usage Examples:
 * 
 * 1. With MenuButton components (Recommended):
 * <Menu
 *   trigger={<button>Open Menu</button>}
 * >
 *   <MenuButton onClick={() => console.log('Option 1')}>
 *     Option 1
 *   </MenuButton>
 *   <MenuButton onClick={() => console.log('Option 2')}>
 *     Option 2
 *   </MenuButton>
 * </Menu>
 * 
 * 2. With custom HTML content:
 * <Menu
 *   trigger={<button>Open Menu</button>}
 * >
 *   <div className="p-2">
 *     <h3 className="font-bold">Custom Content</h3>
 *     <p>This can be any HTML content</p>
 *     <button onClick={() => console.log('Custom action')}>
 *       Custom Button
 *     </button>
 *   </div>
 * </Menu>
 * 
 * 3. With items array (Legacy support):
 * <Menu
 *   items={[
 *     { label: 'Option 1', onClick: () => console.log('Option 1') },
 *     { label: 'Option 2', onClick: () => console.log('Option 2') }
 *   ]}
 *   trigger={<button>Open Menu</button>}
 * />
 */

// Create context for menu actions
const MenuContext = createContext<{ closeMenu: () => void } | null>(null);

const useMenuContext = () => {
  const context = useContext(MenuContext);
  if (!context) {
    throw new Error('useMenuContext must be used within a Menu component');
  }
  return context;
};

/**
 * BGContextMenu - Main component for the context menu
 * 
 * This component renders a context menu that can be triggered by a button or other elements.
 * It supports both static items and dynamic content via children.
 * @param {MenuProps} props - The properties for the menu
 * @returns {JSX.Element} The rendered context menu
 */
const BGContextMenu: React.FC<MenuProps> = ({
  items,
  children,
  trigger,
  className = '',
  menuClassName = '',
  highlightOnHover = false,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const triggerRef = useRef<HTMLDivElement>(null);
  const menuRef = useRef<HTMLDivElement>(null);
  const [menuPosition, setMenuPosition] = useState({ top: 0, left: 0 });

  // Calculate menu position - Simple approach: always position near the trigger
  useEffect(() => {
    if (isOpen && triggerRef.current) {
      const triggerRect = triggerRef.current.getBoundingClientRect();

      // Simple positioning: just below and to the right of the trigger
      const top = triggerRect.bottom + 4; // Small gap below trigger
      const left = triggerRect.right - 200; // Position to align roughly with trigger

      setMenuPosition({ top, left });
    }
  }, [isOpen]);

  // Handle clicks outside the menu
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        menuRef.current &&
        !menuRef.current.contains(event.target as Node) &&
        triggerRef.current &&
        !triggerRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
      return () => document.removeEventListener('mousedown', handleClickOutside);
    }
  }, [isOpen]);

  // Handle escape key
  useEffect(() => {
    const handleEscape = (event: KeyboardEvent) => {
      if (event.key === 'Escape') {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
      return () => document.removeEventListener('keydown', handleEscape);
    }
  }, [isOpen]);

  const handleTriggerClick = () => {
    setIsOpen(!isOpen);
  };

  const closeMenu = () => {
    setIsOpen(false);
  };

  const handleItemClick = (item: MenuItem) => {
    if (item.disabled) return;

    if (item.onClick) {
      item.onClick();
    }
    setIsOpen(false);
  };

  const renderMenuItem = (item: MenuItem, index: number) => {
    const itemClasses = [
      'flex flex-row w-full py-3 px-3 text-sm font-normal rounded-lg transition-colors',
      item.active ? 'bg-mbPrimary-50' : '',
      item.disabled ? 'opacity-50 cursor-not-allowed' : 'hover:bg-gray-100',
      'text-left'
    ].filter(Boolean).join(' ');

    if (item.href && !item.disabled) {
      return (
        <a
          key={index}
          href={item.href}
          className={itemClasses}
          onClick={() => handleItemClick(item)}
        >
          {item.label}
        </a>
      );
    }

    return (
      <button
        key={index}
        type="button"
        className={itemClasses}
        onClick={() => handleItemClick(item)}
        disabled={item.disabled}
      >
        {item.label}
      </button>
    );
  };

  const menu = isOpen && (
    <MenuContext.Provider value={{ closeMenu }}>
      <div
        ref={menuRef}
        className={`
          fixed z-50 w-48 bg-backdrop border border-tragic-200 rounded-lg shadow-lg p-2
          ${menuClassName}
        `}
        style={{
          top: `${menuPosition.top}px`,
          left: `${menuPosition.left}px`,
        }}
        onClick={(e) => {
          // Close menu when clicking on menu items (but not the container itself)
          if (e.target !== menuRef.current) {
            setIsOpen(false);
          }
        }}
      >
        {children || (items && items.map(renderMenuItem))}
      </div>
    </MenuContext.Provider>
  );

  return (
    <>
      <div
        ref={triggerRef}
        className={`relative inline-block ${className}`}
        onClick={handleTriggerClick}
      >
        {trigger}
      </div>
      {createPortal(menu, document.body)}
    </>
  );
};

export default BGContextMenu;
export { MenuButton, useMenuContext };
