/**
 * Simple wrapper around chrome.i18n.getMessage for React components
 * Provides a clean interface to Chrome extension's built-in internationalization
 */
import { chrome } from "@/utils/polyfill";

/**
 * Get a localized message from Chrome extension's _locales
 * @param key - The message key from messages.json
 * @param substitutions - Optional substitutions for placeholders ($1, $2, etc.)
 * @returns Localized string
 */
export const t = (key: string, substitutions?: string | string[]): string => {
  try {
    return chrome.i18n.getMessage(key, substitutions) || key;
  } catch (error) {
    console.warn(`Translation key "${key}" not found:`, error);
    return key;
  }
};

/**
 * Get the current language code
 * @returns Language code (e.g., 'en', 'de', 'es')
 */
export const getCurrentLanguage = (): string => {
  return chrome.i18n.getUILanguage().split('-')[0]; // 'en-US' -> 'en'
};

/**
 * Get the accept languages list
 * @returns Promise resolving to array of language codes
 */
export const getAcceptLanguages = (): Promise<string[]> => {
  return new Promise((resolve) => {
    chrome.i18n.getAcceptLanguages((languages) => {
      resolve(languages);
    });
  });
};

/**
 * Check if a translation key exists
 * @param key - The message key to check
 * @returns True if the key exists
 */
export const hasTranslation = (key: string): boolean => {
  try {
    const message = chrome.i18n.getMessage(key);
    return message !== '';
  } catch {
    return false;
  }
};

export default {
  t,
  getCurrentLanguage,
  getAcceptLanguages,
  hasTranslation
};
