import React, { useContext, useEffect, useReducer, useState } from "react";
import ReactDOM from "react-dom/client";
// Import processed CSS as a string
import cssText from "@/app/popup/src/styles/css-exporter.js";
import { BGSvgIcon } from "@/app/popup/src/components/core/index";
import { sendBackgroundMessage } from "@/utils/messaging/messaging";
import { StartScanRequest, StartScanResponse } from "@/utils/messaging/types";
import { MSG_START_OAP_SCAN } from "@/app/scripts/app-consts";
import { t } from "$Popup/i18n";

console.log("OAP: oap-notification.tsx loaded");

function showOAPNotification() {
    console.log("OAP: createOAPNotification called");

    const shadowHost = document.createElement("div");
    shadowHost.style.position = "fixed";
    shadowHost.style.top = "0";
    shadowHost.style.right = "0";
    shadowHost.style.bottom = "0";
    shadowHost.style.width = "480px";
    shadowHost.style.height = "fit-content";
    shadowHost.style.zIndex = "2147483647";
    shadowHost.style.backgroundColor = "#FFFFFF";
    shadowHost.style.boxShadow = "0 4px 8px 3px rgba(0, 0, 0, 0.08), 0 1px 3px 0 rgba(0, 0, 0, 0.10)";
    shadowHost.style.borderRadius = "16px";
    shadowHost.style.overflow = "hidden";
    shadowHost.style.margin = "16px";
    shadowHost.id = "bg-oap-notification";

    // inject it in the <html> tag
    document.documentElement.appendChild(shadowHost);
    const shadowRoot = shadowHost.attachShadow({ mode: "open" });

    // Inject compiled CSS into shadow DOM
    try {
        const styleSheet = document.createElement("style");
        styleSheet.textContent = cssText;
        shadowRoot.appendChild(styleSheet);
        console.log("OAP: notification CSS injected successfully", cssText.length, "characters");
    } catch (error) {
        console.error("OAP: Error injecting notification CSS:", error);
    }

    const reactContainer = document.createElement("div");
    shadowRoot.appendChild(reactContainer);

    const root = ReactDOM.createRoot(reactContainer);
    root.render(
        <OAPNotification />
    );
}

function dismissOAPNotification() {
    console.log("OAP: dismissOAPNotification called");
    const shadowHost = document.getElementById("bg-oap-notification");
    if (shadowHost) {
        shadowHost.remove();
    }
}

const extensionRoot = chrome.runtime.getURL('app/popup/');

const OAPNotification: React.FC = () => {
    const scanNowClicked = async () => {
        dismissOAPNotification();
        const resp = await sendBackgroundMessage<StartScanRequest, StartScanResponse>(
            { type: MSG_START_OAP_SCAN, payload: { scanType: "google", fromNotification: true } }
        );
    }

    return (<>
        <div className="flex flex-col items-center justify-center bg-backdrop rounded-2xl w-full font-roboto">
            <div className="flex flex-row py-2 items-center justify-between w-full rounded-t-2xl bg-tragic-100 border-solid border-b border-tragic-250">
                <div className="ml-6">
                    <BGSvgIcon iconName="browser-guard-long-logo" width={232} height={16} iconRoot={extensionRoot} />
                </div>
                <a onClick={dismissOAPNotification} className="cursor-pointer mr-2">
                    <BGSvgIcon iconName="close" width={20} height={20} iconRoot={extensionRoot} />
                </a>
            </div>
            <div className="w-full rounded-b-2xl p-6">
                <div className="flex flex-row items-start gap-4 w-full">
                    <BGSvgIcon iconName="oap-notification-icon" width={92} height={92} isNeutral iconRoot={extensionRoot} />
                    <div className="flex flex-col items-start gap-2 grow shrink-0 basis-0">
                        <h5 className="text-base font-medium leading-6 tracking-[0.15px] text-tragic">{t('OAPNotificationTitle')}</h5>
                        <div className="flex flex-col gap-4 items-end">
                            <p className="text-sm font-normal leading-5 tracking-[0.15px] text-tragic opacity-80">{t('OAPNotificationDescription')}</p>
                            <ScanNowButton onClick={scanNowClicked} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>);
}

const ScanNowButton: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <button className="flex flex-row min-w-[88px] px-6 py-2.5 justify-center items-center rounded-[50px] bg-primary cursor-pointer" onClick={onClick}>
            <span className="text-center text-xs font-bold leading-4 tracking-[0.5px] text-backdrop">{t('OAPScanNow')}</span>
        </button>
    );
}

// @ts-ignore
window.showOAPNotification = showOAPNotification;