import React, { forwardRef, useImperativeHandle, useRef } from 'react';
import { Link } from 'react-router-dom';
import { ROUTES } from '$Popup/routes/constants';
import { useAppNavigation } from '$Popup/routes/hooks';
import { BGLinkProps, BGLinkRef } from './types';

/**
 * BGLink Component for Hash-based SPA Navigation and External Links
 * 
 * A flexible navigation component that handles both internal hash-based routing 
 * for browser extensions and external URL navigation.
 * 
 * @example
 * // Hash navigation (default) - changes URL hash
 * <BGLink to="SETTINGS">Go to Settings</BGLink>
 * // Results in: chrome-extension://[id]/app/popup/index.html#/settings
 * 
 * // React Router navigation (same hash, internal routing)
 * <BGLink to="SETTINGS" useReactRouter>Settings Route</BGLink>
 * 
 * // Custom internal path with hash navigation
 * <BGLink path="/custom">Custom Page</BGLink>
 * 
 * // External URL navigation (automatically detected)
 * <BGLink path="https://example.com" target="_blank">External Link</BGLink>
 * // Results in: https://example.com (opens in new tab with security attributes)
 * 
 * // With query parameters
 * <BGLink to="SETTINGS" queryParams={{ tab: 'advanced' }}>Advanced Settings</BGLink>
 */
export const BGLink = forwardRef<BGLinkRef, BGLinkProps>(({
  to,
  path,
  queryParams,
  useReactRouter = false,
  className = '',
  style,
  children,
  onClick,
  disabled = false,
  asButton = false,
  target,
  replace = false,
  'aria-label': ariaLabel,
  rel,
  ...props
}, ref) => {
  const elementRef = useRef<HTMLElement>(null);
  const { navigate } = useAppNavigation();

  useImperativeHandle(ref, () => ({
    focus: () => elementRef.current?.focus(),
    click: () => elementRef.current?.click(),
  }));

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    if (disabled) {
      event.preventDefault();
      return;
    }

    // Call custom onClick if provided
    if (onClick) {
      onClick(event);
    }

    // If path is an external URL, let the browser handle it naturally
    if (path && isExternalUrl(path)) {
      // Don't prevent default - let the browser navigate to the external URL
      return;
    }

    // Handle navigation based on useReactRouter flag
    if (useReactRouter) {
      // Use React Router navigation (internal routing within same hash)
      event.preventDefault();
      const targetPath = getNavigationPath();
      if (targetPath) {
        navigate(targetPath, { replace });
      }
    } else {
      // Use hash navigation (default) - let the anchor handle it naturally
      // The href will be set to the hash URL, so browser handles navigation
    }
  };

  // Check if the path is an external URL
  const isExternalUrl = (url: string) => {
    return url.startsWith('http://') || url.startsWith('https://') || url.startsWith('mailto:') || url.startsWith('tel:');
  };

  // Get the navigation path for React Router
  const getNavigationPath = () => {
    if (path) return path;
    if (to) return ROUTES[to].path;
    return '/';
  };

  // Get the hash URL for anchor href
  const getHashHref = () => {
    // If path is provided and it's an external URL, return it directly
    if (path && isExternalUrl(path)) {
      return path;
    }

    let targetPath = '';
    
    if (to) {
      targetPath = ROUTES[to].path;
    } else if (path) {
      targetPath = path.startsWith('/') ? path : `/${path}`;
    } else {
      targetPath = '/';
    }

    // Add query parameters if provided
    if (queryParams) {
      const params = new URLSearchParams(queryParams);
      targetPath += `?${params.toString()}`;
    }

    // Get clean base URL without context parameter
    const baseUrl = `${window.location.protocol}//${window.location.host}${window.location.pathname}`;
    
    return `${baseUrl}#${targetPath}`;
  };

  // Common props for both button and anchor
  const commonProps = {
    ref: elementRef as any,
    className: `${className} ${disabled ? 'opacity-50 !cursor-not-allowed' : ''}`.trim(),
    style,
    onClick: handleClick,
    'aria-label': ariaLabel,
    'aria-disabled': disabled,
    ...props,
  };

  // If using React Router navigation
  if (useReactRouter && (path || to)) {
    return (
      <Link
        to={getNavigationPath()}
        replace={replace}
        {...commonProps}
        className={`${commonProps.className} no-underline`.trim()}
      >
        {children}
      </Link>
    );
  }

  // Render as button
  if (asButton) {
    return (
      <button
        type="button"
        disabled={disabled}
        {...commonProps}
      >
        {children}
      </button>
    );
  }

  // Render as anchor with hash navigation (default)
  return (
    <a
      href={disabled ? '#' : getHashHref()}
      target={target}
      rel={
        // Automatically add security attributes for external links opened in new tabs
        path && isExternalUrl(path) && target === '_blank' 
          ? (rel ? `${rel} noopener noreferrer` : 'noopener noreferrer')
          : rel
      }
      {...commonProps}
      onClick={disabled ? (e) => e.preventDefault() : commonProps.onClick}
    >
      {children}
    </a>
  );
});

BGLink.displayName = 'BGLink';

export default BGLink;
